"use client";

import React, { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import ProductForm from "@/components/admin/ProductForm";

export default function EditProductPage() {
    const { id } = useParams();
    const [product, setProduct] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetch(`/api/products/${id}`)
            .then((res) => res.json())
            .then((data) => {
                if (data.error) {
                    alert("Product not found");
                } else {
                    setProduct(data);
                }
            })
            .catch((err) => console.error(err))
            .finally(() => setLoading(false));
    }, [id]);

    if (loading) return <div className="p-8 text-center text-text-secondary">Loading...</div>;
    if (!product) return <div className="p-8 text-center text-error">Product not found</div>;

    return <ProductForm initialData={product} isEdit />;
}
